.button {
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
    background-color: #07b3f9;
    border: none;
    color: #fff;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer
}

.error {
    margin-left: auto;
    margin-right: auto
}

.text-center {
    text-align: center
}

.error .clip .shadow {
    height: 180px
}

.error .clip:nth-of-type(2) .shadow {
    width: 130px
}

.error .clip:nth-of-type(1) .shadow,
.error .clip:nth-of-type(3) .shadow {
    width: 250px
}

.error .digit {
    width: 150px;
    height: 150px;
    line-height: 150px;
    font-size: 120px;
    font-weight: 700
}

.error h2 {
    font-size: 32px
}

.error .msg {
    top: -190px;
    left: 30%;
    width: 80px;
    height: 80px;
    line-height: 80px;
    font-size: 32px
}

.error span.triangle {
    top: 70%;
    right: 0;
    border-left: 20px solid #535353;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent
}

.error .container-error-404 {
    margin-top: 10%;
    position: relative;
    height: 250px;
    padding-top: 40px
}

.error .container-error-404 .clip {
    display: inline-block;
    transform: skew(-45deg)
}

.error .clip .shadow {
    overflow: hidden
}

.error .clip:nth-of-type(2) .shadow {
    overflow: hidden;
    position: relative;
    box-shadow: inset 20px 0 20px -15px rgba(150, 150, 150, .8), 20px 0 20px -15px rgba(150, 150, 150, .8)
}

.error .clip:nth-of-type(1) .shadow:after,
.error .clip:nth-of-type(3) .shadow:after {
    content: "";
    position: absolute;
    right: -8px;
    bottom: 0;
    z-index: 9999;
    height: 100%;
    width: 10px;
    background: linear-gradient(90deg, transparent, rgba(173, 173, 173, .8), transparent);
    border-radius: 50%
}

.error .clip:nth-of-type(3) .shadow:after {
    left: -8px
}

.error .digit {
    position: relative;
    top: 8%;
    color: #fff;
    background: #00ad45;
    border-radius: 50%;
    display: inline-block;
    transform: skew(45deg)
}

.error .clip:nth-of-type(2) .digit {
    left: -10%
}

.error .clip:nth-of-type(1) .digit {
    right: -20%
}

.error .clip:nth-of-type(3) .digit {
    left: -20%
}

.error h2 {
    color: #a2a2a2;
    font-weight: 700;
    padding-bottom: 20px
}

.error .msg {
    position: relative;
    z-index: 9999;
    display: block;
    background: #535353;
    color: #a2a2a2;
    border-radius: 50%;
    font-style: italic
}

.error .triangle {
    position: absolute;
    z-index: 999;
    transform: rotate(45deg);
    content: "";
    width: 0;
    height: 0
}

@media(max-width:767px) {
    .error .clip .shadow {
        height: 100px
    }

    .error .clip:nth-of-type(2) .shadow {
        width: 80px
    }

    .error .clip:nth-of-type(1) .shadow,
    .error .clip:nth-of-type(3) .shadow {
        width: 100px
    }

    .error .digit {
        width: 80px;
        height: 80px;
        line-height: 80px;
        font-size: 52px
    }

    .error h2 {
        font-size: 24px
    }

    .error .msg {
        top: -110px;
        left: 15%;
        width: 40px;
        height: 40px;
        line-height: 40px;
        font-size: 18px
    }

    .error span.triangle {
        top: 70%;
        right: -3%;
        border-left: 10px solid #535353;
        border-top: 8px solid transparent;
        border-bottom: 8px solid transparent
    }

    .error .container-error-404 {
        height: 150px
    }
}

